/* **********************************************************************
			woocommerce   (CREADO POR LUIS)
************************************************************************/
:root {
  --colorSubtitulos: #555555;
  --colorParrafo: #777777;
  --colorBorde: #ededed;
  --colorBlanco: white;
  --colorNegro: black;
  --colorNegroTransparente: #00000033; }

.html {
  font-size: 65.5%;
  /*equivalente a 10px*/
  font-family: 'Lato', sans-serif;
  height: 100vh; }

/*---------------SECTION BIENVENIDA-------------*/
.section__bienvenida {
  width: 100%;
  box-sizing: border-box; }

.section__bienvenida-titulo {
  font-weight: 700;
  color: var(--colorSubtitulos);
  font-size: 1.8rem;
  text-align: center; }

.section__bienvenida-parrafo {
  width: 100%;
  font-size: 1.6rem;
  color: var(--colorParrafo);
  text-align: center; }

@media (min-width: 768px) {
  .section__bienvenida-titulo {
    font-size: 2.2rem; }
  .section__bienvenida-parrafo {
    padding-bottom: 6%;
    padding-top: 4%; } }

/*-------------SECTION PRODUCTOS----------------*/
.section__productos {
  width: 83%;
  margin: auto; }

.section__productos-titulo {
  display: block;
  width: 46%;
  margin: 0 auto;
  padding-top: 2%;
  padding-bottom: 2%;
  text-align: center;
  border: 2px solid var(--colorBorde);
  color: var(--colorSubtitulos);
  margin-bottom: 4%; }

.gallery__container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(7, 11.5rem);
  row-gap: 2%;
  padding: 2%; }

.gallery__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: box-shadow 0.2s ease-in-out, bottom 1s ease-in-out; }

.gallery__item:hover {
  box-shadow: 0px 10px 26px var(--colorParrafo);
  bottom: 5px; }

.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.gallery__link {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  list-style: none;
  text-decoration: none;
  font-size: 1.3rem;
  color: var(--colorBlanco);
  text-shadow: 1px 1px 2px var(--colorNegro);
  text-align: center;
  line-height: 11.5rem;
  background-color: var(--colorNegroTransparente);
  font-weight: bold; }

@media (min-width: 768px) {
  .section__productos-titulo {
    width: 28%;
    padding-top: 1.5%;
    padding-bottom: 1.5%; }
  .gallery__container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 11.5rem);
    column-gap: 2.5%; }
  .gallery__item:first-child {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2; } }

@media (min-width: 1024px) {
  .section__productos-titulo {
    width: 22%;
    padding-top: 1.5%;
    padding-bottom: 1.5%; }
  .gallery__container {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 18rem);
    column-gap: 2%; }
  .gallery__link {
    font-size: 1.5rem;
    line-height: 18rem; }
  .gallery__item:first-child {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2; }
  .gallery__item:nth-child(2) {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2; } }

/* **********************************************************************
			woocommerce   (CREADO POR MARCO)
************************************************************************/
.container_woocommerce {
  width: 90%;
  margin-top: 11rem;
  margin-bottom: 3rem;
  padding-left: 1rem; }

.container_wrap {
  padding: 0 1rem; }
  @media only screen and (min-width: 1024px) {
    .container_wrap {
      margin: auto;
      width: 80%;
      margin-bottom: 3rem;
      margin-top: 3rem; } }

.container_wrap:last-child {
  margin-bottom: 0px; }

.woocommerce_products {
  width: 100%;
  margin: auto;
  display: grid;
  column-gap: 10px; }
  @media only screen and (min-width: 768px) {
    .woocommerce_products {
      font-size: 1.5em;
      grid-template-columns: repeat(2, 1fr); } }
  @media only screen and (min-width: 1024px) {
    .woocommerce_products {
      grid-template-columns: repeat(4, 1fr); } }

.woocommerce_products__content {
  flex-basis: 79.5%;
  padding-left: 10px; }

.woocommerce_products__ul {
  display: flex;
  flex-direction: column; }

.woocommerce_products__li {
  list-style: none;
  display: flex;
  margin-top: 10px;
  justify-content: space-between; }

.woocommerce_products__link {
  flex-direction: column;
  text-decoration: none;
  flex-basis: 20%;
  width: 30px;
  /* height: 30px; */ }

.woocommerce_products__img {
  width: 100%;
  height: 100%; }

.woocommerce_products__subtitle {
  display: block; }

.woocommerce_products__price {
  display: block;
  font-weight: 800; }

