/* footer about */
.footer_network__li {
  list-style: none;
  transform: background-color 0.5s ease-in-out, box-shadow 0.5s ease-in-out, borber 0.5s ease-in-out; }
  @media only screen and (min-width: 768px) {
    .footer_network__li {
