  @media only screen and (min-width: 1024px) {
    .footer_network__li {
      margin-left: 2%;
      margin-left: 6%; } }

.footer_network__li:hover > a > i {
  color: #fff; }

.footer_network__li:nth-child(1):hover > a {
  box-shadow: 1px 1px 5px #32529b inset;
  background-color: #3a589d;
  border: 2px solid transparent; }

.footer_network__li:nth-child(2):hover > a {
  box-shadow: 1px 1px 5px #3b6994 inset;
  background-color: #3b6994;
  border: 2px solid transparent; }

.footer_network__li:nth-child(3):hover > a {
  box-shadow: 1px 1px 5px #2478ba inset;
  background-color: #2478ba;
  border: 2px solid transparent; }

.footer_network__li:nth-child(4):hover > a {
